<template>
  <div class="login">
    <van-overlay :show="loading" @click="loading = false" />
    <van-loading color="#1989fa" v-if="loading" class="loading"/>
    <van-form @submit="onSubmit">
      <van-field v-model="username"
                 name="用户名"
                 label="用户名"
                 placeholder="用户名"
                 :rules="[{ required: true, message: '请填写用户名' }]" />
      <van-field v-model="password"
                 type="password"
                 name="密码"
                 label="密码"
                 placeholder="密码"
                 :rules="[{ required: true, message: '请填写密码' }]" />
      <div style="margin: 16px;">
        <van-button round
                    block
                    type="info"
                    native-type="submit">提交</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      username: '',
      password: '',
      loading: false
    }
  },
  methods: {
    loginIn() {
      this.$emit("loginIn", true);
    },
    onSubmit() {
      this.loading = true
      console.log(this.username, this.password);
      setTimeout(() => {
        this.loading = false;
        this.loginIn()
      }, 1000)
    }
  }
};
</script>

<style scoped>
.login {
  height: 100%;
  background: #ddd;
}
.login .loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>
